.container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.title {
  margin-bottom: 1%;
}

.productList {
  display: flex;
}

.productCardContainer {
  width: 250px;
  height: 250px;
  font-size: 0.8rem;
}

.loaderContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
}
.loaderContainer img {
  width: 200px;
}
