.container {
  display: flex;
  flex-direction: column;
}

.menu {
  display: flex;
  margin-bottom: 3%;
}

.quantityContainer {
  display: flex;
  justify-content: center;
  width: 60%;
  align-items: center;
}
.quantityContainer input {
  font-size: 2rem;
  width: 25%;
  text-align: center;
  padding: 1%;
  margin: 0 5%;
}

.quantityContainer button {
  width: 70px;
  height: 40px;
  font-weight: bold;
  font-size: 2rem;
  color: white;
}

.addButtonContainer {
  display: flex;
  width: 40%;
  justify-content: center;
}

.addButtonContainer button {
  font-size: 1.2rem;
  padding: 0px 15px;
}

.total {
  text-align: center;
  font-size: 1.3rem;
  font-style: italic;
}
