.container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}
.formContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #d0d0d0;
  padding: 1%;
  border-radius: 5px;
  position: relative;
  width: 340px;
  height: 380px;
}

.deliveryContainer label:hover,
.giftContainer label:hover,
.formContainer input[type='radio'],
.formContainer input[type='checkbox'],
.formContainer select {
  cursor: pointer;
}

.textInputContainer label {
  display: flex;
  justify-content: space-between;
  width: 240px;
  margin-bottom: 5px;
}

.selectInputContainer label {
  display: flex;
  justify-content: space-between;
  width: 163px;
  margin-bottom: 5px;
}

.textInputSelectContainer label {
  display: flex;
  justify-content: space-between;
  width: 192px;
  margin-bottom: 5px;
}

.textInputSelectContainer label input {
  width: 80px;
}
.deliveryContainer {
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  width: fit-content;
  padding: 5px 0;
  margin-bottom: 5px;
}

.buttonContainer {
  display: flex;
  justify-content: end;
  margin-top: 20px;
}

.buttonContainer button {
  padding: 5px 15px;
}

.error {
  border: 1px solid red;
}

.closeForm {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.closeForm::before,
.closeForm::after {
  position: absolute;
  top: 0;
  left: 50%;
  content: '';
  display: block;
  width: 2px;
  height: 100%;
  background: black;
  transform: rotate(45deg);
}

.closeForm::before {
  transform: rotate(45deg);
}

.closeForm::after {
  transform: rotate(-45deg);
}
