.container {
  position: fixed;
  display: flex;
  justify-content: center;
  background: #eaeaea;
  margin-top: -90px;
  z-index: 90;
  padding: 1% 4%;
  border-radius: 0 0 10px 10px;
  box-shadow: 0px 3px 5px #ccc;
}

.sortBy {
  margin-right: 4%;
  flex-shrink: 0;
	align-self: center;
}

.from,
.to {
  width: 25%;
  text-align: right;
}

.btnContainer button {
  padding: 15%;
}

.limitPrice {
  flex-shrink: 0;
  margin-right: 1%;
  align-self: center;
}
