.container {
  position: fixed;
  display: flex;
  justify-content: center;
  height: 100px;
  width: 100%;
  z-index: 100;
  background: #eaeaea;
  box-shadow: 0px 1px 10px #ccc;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1200px;
  padding: 1%;
}
