.imageContainer {
  overflow: hidden;
  position: relative;
  width: 150px;
  height: 150px;
  margin-right: 30px;
  cursor: pointer;
  z-index: 1;
}

.imageContainer:hover {
  scale: 1.05;
  z-index: 2;
}

.imageContainer:last-child {
  margin-right: 0px;
}

.imageContainer img {
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
