.container {
  position: relative;
}

.title {
  position: absolute;
  background: white;
  border: 1px solid black;
  border-radius: 5px;
  padding: 1px 4px;
  font-size: 0.8rem;
  z-index: 1;
  pointer-events: none;
}

.title::after {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  position: absolute;
  background: white;
  transform: rotate(45deg);
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  bottom: -3.9px;
  right: 5px;
}
