.container {
  position: fixed;
  padding: 1% 2%;
  background: whitesmoke;
  border-radius: 5px;
  box-shadow: 1px 1px 1px #ccc;
}
.title {
  font-size: 1.2rem;
  margin-bottom: 5%;
  text-shadow: 1px 1px 2px #ccc;
  text-decoration: underline;
}
.category {
  margin-bottom: 10px;
}

.category:hover {
  background: #ccc;
}

.category a {
  display: block;
  width: 100%;
  text-decoration: none;
  color: black;
}

.active {
  font-weight: bold;
}
