.container {
  display: flex;
  margin-bottom: 2%;
  background: whitesmoke;
  box-shadow: 1px 1px #ccc;
  padding: 1%;
}

.container a {
  display: flex;
  color: black;
  text-decoration: none;

  width: 100%;
}

.container a:hover {
  scale: 1.01;
}

.checkboxContainer {
  padding: 2%;
  margin: auto 0;
}

.checkboxContainer input {
  display: block;
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.imgContainer {
  overflow: hidden;
  position: relative;
  width: 200px;
  height: 150px;
}

.imgContainer img {
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.descriptionContainer {
  display: flex;
  flex-direction: column;
  padding: 1% 3%;
  width: 100%;
}
.editQty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 3% 0 4%;
  width: 30%;
}
.buttonContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
}

.imgTrash {
  cursor: pointer;
}

.imgTrash:hover {
  scale: 1.1;
}

.title {
  font-weight: bold;
  font-size: 1.2rem;
}

.description {
  font-style: italic;
}

.totalPrice {
  margin-top: auto;
}
.totalPrice__sum {
  font-weight: bold;
}
