.container {
  width: 100%;
  padding: 120px 0 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.backButtonContainer {
  width: 100%;
  margin-bottom: 1%;
}
.backButtonContainer button {
  padding: 1%;
  font-size: 1.2rem;
}

.title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 1%;
}

.description {
  font-size: 1.3rem;
  font-style: italic;
  margin-bottom: 1%;
}

.price {
  font-size: 3rem;
  margin-bottom: 0.5%;
  font-weight: bold;
}
.stock {
  font-size: 1.2rem;
  font-style: italic;
  margin-bottom: 5%;
}

.addCardContainer {
  display: flex;
  justify-content: center;
  width: 45%;
}
