.container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: fixed;
  bottom: 40px;
  margin-left: 1220px;
  cursor: pointer;
  font-size: 40px;
  border: 2px solid gray;
  width: 50px;
  height: 50px;
  border-radius: 5px;
  background-color: whitesmoke;
  box-shadow: 0 0 3px 1px #ccc;
}

.container:hover {
  scale: 1.05;
}
