.container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  border: 1px solid black;
  padding: 5%;
  text-decoration: none;
  background: white;
  z-index: 1;
}
.container:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 5px black;
  z-index: 2;
}

.title {
  text-align: center;
  border-bottom: 1px solid black;
  margin-bottom: 5%;
  padding-bottom: 2%;
}

.description {
  margin-bottom: 5%;
}

.imageContainer {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  margin-bottom: 5%;
}

.image {
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.price {
  font-size: 2rem;
  margin-bottom: 3%;
}

.rating {
  position: absolute;
  top: 1%;
  right: 3%;
}

.star {
  color: darkgoldenrod;
}
