.container {
  position: relative;
  left: 45px;
  display: flex;
  align-items: center;
  margin-bottom: 1%;
}

.rating {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-right: 5%;
}

.chooseRating {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  position: relative;
  z-index: 10;
}
.currentRating:hover {
  visibility: hidden;
}
.chooseRating:hover + div {
  visibility: hidden;
}

.currentRating {
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: 11;
}

.currentRating::before {
  content: '★★★★★';
  font-size: 50px;
  color: gold;
}

.ratingValue {
  position: relative;
  top: 4px;
  width: 95px;
  font-size: 30px;
}

.clearVote {
  border: none;
  background: none;
  position: absolute;
  top: 55px;
  right: 0;
  cursor: pointer;
  font-weight: bold;
}

.clearVote:hover {
  text-decoration: underline;
}
