.container {
  background: whitesmoke;
  padding: 2%;
  box-shadow: 1px 1px 1px #ccc;
  position: relative;
}

.container input {
  display: block;
}

.nickname {
  width: 35%;
  font-size: 1.5rem;
  margin-bottom: 0.3%;
  padding: 0 1%;
}

.text {
  width: 100%;
  height: 220px;
  font-size: 1.2rem;
  resize: none;
  margin-bottom: 0.3%;
  padding: 1%;
}

.buttonContainer {
  display: flex;
  justify-content: end;
}
.buttonContainer button {
  padding: 1%;
  font-size: 1.2rem;
}

.errorMessage {
  height: 20px;
  color: red;
  margin-bottom: 1%;
}

.errorFocus {
  border: 3px solid red;
}

.loadingReview {
  position: absolute;
  height: 146.42px;
  width: 100%;
  background: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  bottom: -170px;
  box-shadow: 1px 1px 1px #ccc;
  z-index: 10;
}
.loadingReview img {
  width: 100px;
}
