.container {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  border: 1px solid black;
  border-radius: 5px;
  padding: 1%;
  margin: 1%;
}

.search {
  width: 100%;
  border-radius: 5px;
  font-size: 30px;
  background: transparent;
  border: none;
  outline: none;
}
