.container {
  position: relative;
  display: flex;
  width: 500px;
  height: 500px;
  overflow: hidden;
}

.mainSlide {
  position: absolute;
  display: flex;
  left: -200px;
  transition: all 0.5s;
}

.slide {
  width: 500px;
  height: 500px;
  overflow: hidden;
  position: relative;
}

.slide img {
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.buttonPrev,
.buttonNext {
  position: absolute;
  z-index: 5;
  top: 45%;
  border-radius: 50%;
  width: 10%;
  height: 10%;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.6);
  box-shadow: 0px 0px 5px 0px black;
}

.buttonPrev {
  left: 10px;
}

.buttonNext {
  right: 10px;
}

.buttonPrev::before,
.buttonNext::before {
  position: absolute;
  top: 25%;
  content: '';
  display: block;
  width: 50%;
  height: 50%;
  rotate: 45deg;
  border-left: 3px solid white;
  border-bottom: 3px solid white;
}
.buttonPrev::before {
  rotate: 45deg;
  left: 30%;
}
.buttonNext::before {
  right: 30%;
  rotate: 225deg;
}

.buttonPrev:hover,
.buttonNext:hover {
  scale: 1.1;
}
