.container {
  margin-bottom: 1%;
  background: whitesmoke;
  padding: 1.5%;
  box-shadow: 1px 1px 1px #ccc;
  display: flex;
}

.main {
  padding-left: 1.5%;
  width: 100%;
}

.userName {
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 1%;
}

.text {
  font-size: 1.1rem;
  margin-bottom: 2%;
}

.likeButton,
.dislikeButton {
  width: 25px;
  cursor: pointer;
}

.likeButton:hover,
.dislikeButton:hover {
  scale: 1.1;
}

.dislikeButton {
  transform: rotate(180deg) translateY(-38%);
  margin-left: 0.5%;
}
